}

.GMX-Interface {
  &.App {
    position: relative;
    // overflow: hidden;
    min-height: 100%;
    font-size: 17px;
    line-height: calc(22.1/17);
    font-weight: 500;
    /* font-size: 1.5rem; */
  }
  
  .App-header-backdrop {
    position: fixed;
    z-index: 10;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  
  .App-content header {
    position: relative;
    z-index: 800;
  }
  
  .App-header.large {
    height: 6.2rem;
    backdrop-filter: blur(4px);
    display: flex;
    justify-content: space-between;
    position: relative;
    padding-left: 3.2rem;
    padding-right: 3.2rem;
    background: transparent 0% 0% no-repeat padding-box;
    border-bottom: 1px solid #ffffff10;
  }
  
  .App-header.large::after {
    height: 1.3rem;
    width: 100%;
    content: " ";
    background: transparent linear-gradient(180deg, #00000030 0%, #00000000 100%) 0% 0% no-repeat padding-box;
    position: absolute;
    bottom: -1.3rem;
    left: 0;
  }
  
  .App-header.large .App-header-links {
    display: flex;
  }
  
  .App-header.small {
    display: none;
  }
  
  .App-header-link-home {
    display: none;
  }
  
  .App-header-drawer {
    background: #17182c88 0% 0% no-repeat padding-box;
    box-shadow: 0.8rem 0.3rem 0.6rem #00000029;
    opacity: 1;
    backdrop-filter: blur(2.7rem);
    position: fixed;
    z-index: 801;
    left: 0;
    right: 0;
    top: 0;
    height: 100vh;
    width: 30.4rem;
    overflow-y: auto;
    -ms-overflow-style: none; /* Internet Explorer 10+ */
    scrollbar-width: none; /* Firefox */
    max-width: 100%;
  }
  .App-header-drawer::-webkit-scrollbar {
    display: none; /* Safari and Chrome */
  }
  
  .App-header-drawer .App-header-link-container .a,
  .App-header-drawer .App-header-link-container a {
    font-size: 1.4rem;
    line-height: 1.8rem;
    font-weight: normal;
    letter-spacing: 0.1px;
    color: #a0a3c4;
    padding: 1.5rem 1.6rem;
    text-decoration: none;
    display: block;
  }
  
  .App-header-drawer .App-header-link-container .a:hover,
  .App-header-drawer .App-header-link-container .a:focus,
  .App-header-drawer .App-header-link-container .a:active,
  .App-header-drawer .App-header-link-container a:hover,
  .App-header-drawer .App-header-link-container a:focus,
  .App-header-drawer .App-header-link-container a.active {
    background: #303fd024;
    color: white;
  }
  
  .App-header-links-header {
    height: 6.2rem;
    display: flex;
    align-items: center;
    z-index: 3;
    margin-left: 1.6rem;
    justify-content: space-between;
  }
  
  .App-header-container-left {
    display: flex;
    align-items: center;
  }
  
  .App-header-top {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    /* z-index: 2; */
  }
  
  .App-header-menu-icon-block {
    margin-right: 1.2rem;
    margin-left: 0px;
    display: flex;
    align-items: center;
  }
  
  .App-header-menu-icon {
    color: white;
    font-size: 2rem;
    cursor: pointer;
    opacity: 0.7;
    margin: 0.9rem 1rem;
  }
  
  .App-header-menu-icon:hover {
    opacity: 0.9;
  }
  
  .App-highlight-box {
    background: linear-gradient(
      45deg,
      rgba(80, 10, 245, 1) 0%,
      rgba(43, 118, 224, 1) 35%,
      rgba(7, 157, 250, 1) 77%,
      rgba(2, 207, 207, 1) 100%
    );
    border-radius: 0.3rem;
    box-shadow: 0 0 0.4rem 0.6rem rgba(45, 66, 252, 0.15);
  }
  
  .App-box,
  .App-card,
  .App-card-primary,
  .App-box-highlight,
  .App-box-solid {
    position: relative;
    border-radius: 5px;
    background: #0D0F29;
    /* font-size: 1.5rem; */
  }
  
  .App-card-bottom-placeholder {
    visibility: hidden;
  }
  
  .App-card-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1.5rem;
    padding-bottom: 1.8rem;
  }
  
  .App-card-bottom .App-card-divider,
  .App-card-bottom-placeholder .App-card-divider {
    margin-bottom: 1.8rem;
  }
  
  .App-card-long {
    background: #16182e;
    margin-bottom: 2.4rem;
  }
  
  .App-card-long-content {
    display: flex;
    padding: 1.6rem 0;
  }
  
  .App-card-long_sub {
    width: 20%;
    padding: 0 1.6rem;
  }
  
  .App-card-long_sub__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 0.8rem;
  }
  
  .App-card-long_sub__info___title {
    font-size: 1.6rem;
    line-height: 2.1rem;
    font-weight: normal;
    letter-spacing: 0px;
    color: #ffffff;
  }
  
  .App-card-long_sub__info___subtitle {
    font-size: 1.2rem;
    line-height: 1.5rem;
    font-weight: normal;
    letter-spacing: 0px;
    color: #a0a3c4;
  }
  
  .App-card-long-sub-left {
    display: flex;
  }
  
  .App-card-long_sub-icon {
    display: flex;
  }
  
  .App-card-long_sub__iconlist .App-card-long_sub__iconlist___icon {
    margin-left: 0.8rem;
  }
  
  .App-card-long_sub__iconlist .App-card-long_sub__iconlist___icon img {
    filter: grayscale(1);
  }
  
  .App-card-long_sub__iconlist .App-card-long_sub__iconlist___icon img:hover {
    filter: unset;
  }
  
  .App-card-long_sub__iconlist {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  
  .App-card-divider-vertical {
    width: 1px;
    height: 8.8rem;
    background: #a0a3c4;
  }
  
  .App-card-long_sub:first-child {
    display: flex;
    align-items: center;
  }
  
  .App-card-long_sub__title {
    color: #a0a3c4;
    font-size: 1.4rem;
    line-height: 1.8rem;
    font-weight: normal;
    letter-spacing: 0.25px;
    margin-bottom: 0.8rem;
  }
  
  .App-card-long_sub__subtitle {
    letter-spacing: 0.36px;
    color: white;
    font-size: 2rem;
    line-height: 2.6rem;
    font-weight: normal;
  }
  
  .App-card-title-block {
    display: flex;
    justify-content: space-between;
    padding: 1.6rem;
  }
  
  .App-card-title-info-icon {
    display: flex;
    margin-right: 0.8rem;
  }
  
  .App-card-title-info {
    display: flex;
  }
  
  .App-card-info-title {
    font-size: 1.6rem;
    line-height: 2.1rem;
    font-weight: normal;
    letter-spacing: 0px;
    color: #ffffff;
  }
  
  .App-card-info-subtitle {
    font-size: 1.2rem;
    line-height: 1.5rem;
    font-weight: normal;
    letter-spacing: 0px;
    color: #a9a9b0;
  }
  
  .App-card-title-iconlist {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 0.8rem;
    grid-row-gap: 0.8rem;
  }
  
  .App-card-title-iconlist___icon img {
    filter: grayscale(1);
  }
  
  .App-card-title-iconlist___icon img:hover {
    filter: unset;
  }
  
  .Wallet-btn {
    display: block;
    box-shadow: inset 0px 0px 1rem 0.5rem rgba(27, 30, 46, 0.2);
    background: var(--dark-blue);
    height: 5.1rem;
    width: 26.35rem;
    border: 1px solid var(--dark-blue-border);
    border-radius: 0.3rem;
    position: relative;
    color: white;
    font-size: 2rem;
    display: grid;
    grid-template-columns: auto 1fr;
    text-align: left;
    padding-left: 1.85rem;
    padding-top: 1.05rem;
  }
  .Wallet-btn:hover {
    background: var(--dark-blue-hover);
    border-color: var(--dark-blue-border);
  }
  .Wallet-btn:active {
    background: var(--dark-blue-active);
  }
  
  .Wallet-btn:not(:last-child) {
    margin-bottom: 1.05rem;
  }
  
  .MetaMask-btn img {
    display: inline-block;
    height: 2.325rem;
    margin-right: 1.7rem;
    margin-left: 0.15rem;
  }
  
  .CoinbaseWallet-btn img {
    display: inline-block;
    height: 2.8rem;
    margin-right: 1.7rem;
  }
  
  .WalletConnect-btn img {
    display: inline-block;
    height: 2.8rem;
    margin-right: 1.8rem;
  }
  
  button.App-connect-wallet {
    cursor: pointer;
    display: inline-block;
    border: none;
    font-size: 1.4rem;
    line-height: 2rem;
    font-weight: 500;
    padding: 0.9rem 2.4rem;
    height: 3.6rem;
    color: #17182c;
    background: #03cfcd 0% 0% no-repeat padding-box;
    border-radius: 0.4rem;
  }
  
  button.App-connect-wallet:hover {
    opacity: 1;
  }
  
  .App-header-trade-link {
    margin-right: 2.4rem;
  }
  
  .App-header-user-address {
    border: 1px solid #ffffff29;
    height: 3.6rem;
    border-radius: 0.4rem;
    display: inline-flex;
    align-items: center;
    color: white;
    position: relative;
  }
  
  .mobile-menu-divider {
    height: 1px;
    background: #4e516a;
  }
  
  .App-card-divider {
    height: 1px;
    background: #23263b;
    margin: 1.05rem -1.5rem;
  }
  
  .App-box-solid {
    background: linear-gradient(90deg, rgba(20, 21, 38, 1) 0%, rgba(25, 27, 47, 1) 100%);
  }
  
  .Page {
    max-width: 108.5rem;
    margin: auto;
    padding-top: 4.65rem;
  }
  
  .Page-content {
    padding: 4.65rem;
    padding-top: 1.5rem;
  }
  
  .section-title-block {
    display: flex;
    margin-bottom: 2.325rem;
    max-width: 58.4rem;
    width: 100%;
  }
  
  .page-main-content .section-title-block {
    margin-top: 6.2rem;
  }
  
  .section-title-icon {
    /* margin-right: 1.2rem; */
    display: flex;
    align-items: center;
  }
  
  .section-title-icon img {
    /* display: flex; */
    display: none;
  }
  
  .section-title-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .section-title-content .section-title-content__title {
    font-size: 2.325rem;
    line-height: 3.1rem;
    color: white;
    letter-spacing: 0px;
  }
  
  .section-title-content .section-title-content__description {
    font-size: 1.8rem;
    line-height: 2.3rem;
    letter-spacing: 0px;
    color: #a0a3c4;
    margin-top: 0.4rem;
    max-width: 62rem;
  }
  
  .section-title-content .section-title-content__description span {
    cursor: pointer;
    color: white;
  }
  
  .Page-title-section {
    padding-left: 4.65rem;
    padding-right: 1.5rem;
    margin-top: 1.5rem;
    position: relative;
  }
  
  .Page-title-section:first-child {
    margin-top: 0rem;
  }
  
  .Page-title {
    font-size: 3.4rem;
    font-weight: 500;
    margin-bottom: 0.8rem;
    font-weight: bold;
  }
  
  .Page-description {
    /* opacity: 0.7; */
    color: #b7b7bd;
    line-height: 2.15rem;
    font-size: 1.5rem;
  }
  
  .Page-description span,
  .Page-description a {
    text-decoration: underline;
    cursor: pointer;
    display: inline-flex;
    color: inherit;
  }
  
  .Page-description span img,
  .Page-description a img {
    margin-left: 0.4rem;
  }
  
  .App-hero {
    position: relative;
    text-align: center;
    padding-top: 6.2rem;
    margin-bottom: 3.1rem;
  }
  
  .App-hero-primary {
    font-size: 4.65rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .App-note {
    text-align: center;
    font-weight: bold;
    font-size: 1.4rem;
    padding: 0 3.1rem;
  }
  
  .App-card {
    padding: 1.5rem 2rem;
    border-radius: 5px;
    border-top-left-radius: 0;
    font-size: 15px;
    line-height: calc(19.5/15);

    &-divider {
      opacity: 0;
      margin: 0.5rem 0;

      &:first-of-type {
        display: none;
      }
    }
  }
  
  .App-card-title {
    font-size: 1.6rem;
    line-height: 2.1rem;
    font-weight: normal;
    letter-spacing: 0px;
    color: #ffffff;
    margin-bottom: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .App-card-title-small {
    font-size: 1.7rem;
    margin-bottom: 1.5rem;
  }
  
  .App-card-content {
    display: grid;
    grid-row-gap: 0.8rem;
  }
  
  .App-card-row {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-column-gap: 1.5rem;
    padding: unset;
  }
  .App-card-row .label {
    color: #384263;
    
    & + div {
      color: #747FA6;
    }
  }
  
  .App-card-row.inner {
    padding-left: 1.5rem;
  }
  .App-card-row .icon {
    vertical-align: baseline;
    margin-left: 0.31rem;
    position: relative;
    top: 2px;
  }
  
  .App-card-row > div:last-child {
    text-align: right;
  }
  
  .App-card-options {
    padding: unset;
    // margin: -0.62rem;
  }
  
  .App-card-option {
    margin: 0.62rem;
  }
  
  .App-header a.App-header-link-main,
  .App-header .App-header-link-main {
    font-size: 1.7rem;
    padding: 1.5rem;
    margin-left: -1.5rem;
    display: flex;
    margin-right: 1.2rem;
  }
  
  .App-header-link-main img {
    display: inline-block;
    height: 2.15rem;
    vertical-align: middle;
  }
  
  .App-header-link-main img.small {
    display: none;
  }
  
  .App-header .a,
  .App-header a {
    text-decoration: none;
    vertical-align: middle;
    display: inline-block;
    cursor: pointer;
  }
  
  .App-header .a.active,
  .App-header a.active {
    pointer-events: none;
    opacity: 1;
    color: #eee;
  }
  
  .App-header .a:hover,
  .App-header a:hover {
    opacity: 1;
    color: #eee;
  }
  
  .App-header-container-left .a,
  .App-header-container-left a {
    padding: 1.8rem 1.7rem;
    display: block;
    color: #a0a3c4;
    font-size: 1.5rem;
    line-height: 1.8rem;
    font-weight: normal;
  }
  
  .App-header-container-left .a.active,
  .App-header-container-left a.active {
    color: white;
  }
  
  .Toastify {
    font-size: 1.4rem;
  }
  
  .Toastify__toast {
    min-height: 4.65rem;
    cursor: default;
  }
  
  .Toastify__toast-body {
    padding: 0 0.465rem;
  }
  
  .Toastify-debug {
    margin-top: 0.465rem;
    font-size: 1.24rem;
    opacity: 0.7;
  }
  
  .Toastify-debug-button {
    display: inline-block;
    border-bottom: 1px dashed;
    cursor: pointer;
  }
  
  .Toastify-debug-content {
    margin-top: 4px;
    padding-bottom: 8px;
    max-width: 300px;
    overflow-x: auto;
  }
  
  .App-icon {
    vertical-align: middle;
    margin-bottom: 0.465rem;
  }
  
  .App-background-side-1 {
    z-index: 1;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    pointer-events: none;
    background: linear-gradient(90deg, rgba(45, 66, 252, 0.05) 0%, rgba(8, 8, 10, 0) 100%);
  }
  
  .App-background-side-2 {
    z-index: 2;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    pointer-events: none;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 216, 240, 0.01) 51%, rgba(0, 216, 240, 0.03) 100%);
  }
  
  .App-background {
    z-index: 4;
    position: absolute;
    left: -100%;
    right: 0%;
    top: -20%;
    bottom: -200%;
    pointer-events: none;
    background: radial-gradient(circle, rgba(80, 10, 245, 0.05) 0%, rgba(80, 10, 245, 0.02) 36%, rgba(8, 8, 10, 0) 100%);
  }
  
  .App-background-ball-1 {
    z-index: 3;
    position: absolute;
    left: 0%;
    right: 0%;
    top: -50%;
    bottom: -200%;
    pointer-events: none;
    background: radial-gradient(circle, rgba(102, 56, 217, 0.05) 0%, rgba(56, 33, 105, 0.02) 36%, rgba(8, 8, 10, 0) 100%);
  }
  
  .App-background-ball-2 {
    z-index: 2;
    position: absolute;
    left: -50%;
    right: -150%;
    top: -30%;
    bottom: -200%;
    pointer-events: none;
    background: radial-gradient(circle, rgba(7, 149, 250, 0.05) 0%, rgba(7, 149, 250, 0.02) 36%, rgba(8, 8, 10, 0) 100%);
  }
  
  .App-highlight {
    z-index: 0;
    position: absolute;
    left: 0%;
    right: -100%;
    top: -100%;
    bottom: 0%;
    pointer-events: none;
  }
  
  .App-title {
    text-align: center;
    font-size: 4.65rem;
    margin-top: 17vh;
    margin-bottom: 1.5rem;
    font-weight: bold;
  }
  
  .App-subtitle {
    text-align: center;
    font-size: 1.7rem;
    margin-bottom: 7.75rem;
    opacity: 0.9;
    padding: 0 1.5rem;
  }
  
  .App-info-row {
    display: grid;
    grid-template-columns: auto auto;
    font-size: 1.4rem;
    margin-bottom: 0.465rem;
  }
  
  .App-info-label {
    opacity: 0.7;
  }
  
  .App-cta {
    text-align: center;
    cursor: pointer;
    display: inline-block;
    padding: 1.5rem;
    border-radius: 0.3rem;
    border: none;
