  button.App-button-option:hover:enabled {
    background: linear-gradient(90deg, rgba(58, 78, 252, 1) 0%, rgba(51, 68, 222, 1) 100%);
    box-shadow: 0 0 0.4rem 0.6rem rgba(45, 66, 252, 0.2);
  } */
  
  button.App-button-option:disabled {
    cursor: not-allowed;
